<template>
    <div class="login">

        <div class="login-area">
            <div class="company-title clearfix">

                <div class="text">成都市蓉城出租汽车有限公司</div>
                <div class="img">
                    <img src="../assets/images/yz.png" alt="">
                    <img src="../assets/images/taxi.png" alt="">
                </div>

            </div>
            <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px"
                     class="login-container">
                <h3 class="title">系统登录</h3>
                <el-form-item prop="account" style="margin-bottom:20px;">
                    <el-input class="input-item" type="text" v-model="ruleForm2.account" auto-complete="off"
                              placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item prop="password" style="margin-bottom:20px;">
                    <el-input @keyup.enter.native="loginSubmit" class="input-item" type="password"
                              v-model="ruleForm2.password" auto-complete="off" placeholder="密码"></el-input>
                </el-form-item>
                <!--<el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>-->
                <el-form-item style="width:100%;">
                    <el-button type="success" style="width:100%;font-size:20px;font-weight: bold;"
                               @click.native.prevent="loginSubmit" :loading="logining">
                        登 录
                    </el-button>
                </el-form-item>
            </el-form>
        </div>

    </div>
</template>

<script>
    export default {
        data() {
            return {
                logining: false,
                ruleForm2: {
                    account: '',
                    password: ''
                },
                rules2: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                    ],
                    password: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                    ]
                },
                checked: true,
                $rzdm: '',
                userInfo: {},
                company: {}
            };
        },
        methods: {
            getCompany() {
                let url = this.$url + '/company/findAll';
                this.$axios.post(url, this.$qs.stringify({rzdm: this.$rzdm})).then((res) => {
                    let data = res.data;
                    this.logining = false;
                    if (data.resCode == 200) {
                        let companyData = data.data;
                        for (let i = 0; i < companyData.length; i++) {
                            let item = companyData[i];
                            this.company[item.gsbm] = item.gsmc;
                        }
                        this.company[this.userInfo.gsdm] = this.userInfo.gsmc;
                        sessionStorage.setItem('company', JSON.stringify(this.company));
                        this.$router.push({path: '/Search'});
                    }
                    else if (data.resCode == 100) {
                        this.$router.push('/login');
                    }
                    else {
                        this.$message.error(res.data.resMsg);
                    }
                });
            },
            loginSubmit(ev) {
                this.$refs.ruleForm2.validate((valid) => {
                    if (valid) {
                        this.logining = true;
                        var loginParams = {account: this.ruleForm2.account, password: this.ruleForm2.password};
                        let url = this.$url + '/user/login';
                        this.$axios.post(url, this.$qs.stringify(loginParams)).then((res) => {

                            var data = res.data;
                            if (data.resCode == 200) {
                                sessionStorage.setItem('userInfo', JSON.stringify(data.data));
                                this.$rzdm = data.data.code;
                                this.userInfo = data.data.user;
                                this.getCompany();
                            } else {
                                this.$message.error(res.data.resMsg);
                                this.ruleForm2.password = '';
                            }
                        });
                    }
                });
            }
        }
    }

</script>

<style lang="scss" scoped>
    .login {
        position: relative;
        width: 100%;
        height: 100%;
        background: url("../assets/images/login-bg.jpg");
        overflow: auto;
        .login-area {
            position: relative;
            width: 1100px;
            margin: auto;
            .company-title {
                position: absolute;
                top: 280px;
                color: #fff;
                font-size: 48px;
                font-weight: bold;
                font-family: "Songti SC";
                /*width: 560px;*/
                .text {
                    text-shadow: 5px 5px 5px green;
                    margin-left: 25px;
                    text-align: center;
                    vertical-align: middle;
                }
                .img {
                    margin-top: 35px;
                    width: 100%;
                    text-align: center;
                    padding-left: 70px;
                    img {
                        margin: 25px;
                        float: left;
                        width: 150px;
                    }
                }

            }
            .login-container {
                position: absolute;
                right: 0px;
                top: 230px;
                width: 320px;
                padding: 35px 40px;
                background: #0bb81f;
                box-shadow: 0 0 25px #0b7f17;
                border-radius: 5px;
                .title {
                    font-size: 30px;
                    margin: 0px auto 20px auto;
                    text-align: center;
                    color: #fff;
                }

                .remember {
                    margin: 0px 0px 35px 0px;
                }
                .input-item {
                    input {
                        background: transparent;
                    }
                    font-size: 20px !important;
                    font-weight: bold;
                    color: #000;
                }
            }
        }

    }

</style>


