<template>
    <section>
        <!--工具条-->
        <el-col :span="24" class="toolbar toolbar2" >
            <el-form :inline="true" size="medium">
                <el-form-item style="margin: auto;">
                    <el-button type="primary" @click="handleAdd">添加新用户</el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!--列表-->
        <el-table size="medium"  :data="tableData" v-loading="listLoading"  @selection-change="selsChange" style="width: 100%;">
            <el-table-column prop="xm" label="姓名"></el-table-column>
            <el-table-column prop="zh" label="账号"></el-table-column>
            <el-table-column prop="gsdm" label="所属公司" :formatter="formatterCompany"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope"  v-if="scope.row.zh !== 'admin'">
                    <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="mini" @click="resetPwd(scope.$index, scope.row)">修改密码</el-button>
                    <el-button size="mini" type="danger" @click="handleDel(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--工具条-->
        <el-col :span="24" class="bottom-toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="size"
                           :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!--新增界面-->
        <el-dialog title="用户信息" :visible.sync="addFormVisible" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="120px" ref="addForm">

                <el-form-item
                        label="账号"
                        prop="zh"
                        :rules="[{ required: true, message: '账号号不能为空', trigger: 'blur' }]">
                    <el-input v-model="addForm.zh"></el-input>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="addForm.xm"></el-input>
                </el-form-item>
                <el-form-item label="密码" v-show="passwordShow"
                              prop="mm"
                              :rules="[{ required: passwordShow, message: '密码不能为空', trigger: 'blur' }]">
                    <el-input v-model="addForm.mm"></el-input>
                </el-form-item>
                <el-form-item label="所属公司">
                    <el-select v-model="addForm.gsdm" style="width:100%;">
                        <el-option v-for="(item,index) in company" :label="item" :key="index" :value="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="备注">
                    <el-input type="textarea" v-model="addForm.bz"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>

        <el-dialog title="重置密码" :visible.sync="resetPassword" :close-on-click-modal="false">
            <el-form label-width="120px" ref="addForm">
                <el-form-item
                        label="账号">
                    <el-input v-model="addForm.zh" readonly></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="newPassword"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="resetPassword = false">取消</el-button>
                <el-button type="primary" @click.native="resetPwdSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    export default {
        data() {
            return {
                //公司代码修改后同步修改此处,此代码作为管理员权限代码
                passwordShow: true,
                newPassword: '',
                company:JSON.parse(sessionStorage.getItem('company')),
                tableData:[],
                total: 0,
                page: 1, //当前页
                size: 10, //每页条数
                listLoading: false,
                addFormVisible: false,//
                resetPassword: false,//
                addLoading: false,
                addFormRules: {
                    name: [
                        {required: true, message: '请输入姓名', trigger: 'blur'}
                    ]
                },
                //新增界面数据
                addForm: {},
            }
        },
        created(){
           this.getData();
        },
        methods: {
            formatterCompany(row){
                return this.company[row.gsdm];
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            //获取数据
            getData() {
                let param = {
                    rzdm: this.$rzdm,
                    page: this.page,
                    size: this.size
                };
                let url = this.$url + '/user/findAllByPage';
                this.listLoading = true;
                this.$axios.post(url,this.$qs.stringify(param)).then((res) => {
                    this.listLoading = false;
                    let data = res.data;
                    //console.log(data);
                    if(data.resCode == 200){
                        this.total = data.data.totalElements;
                        this.tableData = data.data.content;
                    }else{
                        this.$message.error(res.data.resMsg);
                    }
                });
            },
            //删除
            handleDel: function (index, row) {
                this.$confirm('确认删除该记录吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    let url = this.$url + '/user/delete';
                    this.$axios.post(url,this.$qs.stringify({id:row.id,rzdm:this.$rzdm})).then(res=>{
                        if(res.data.resCode == 200){
                            this.getData()
                            this.listLoading = false;
                            this.$message({
                                message: '删除成功',
                                type: 'success'
                            });
                        }else{
                            this.$message.error(res.data.resMsg);
                        }
                    })
                })
            },
            //显示编辑界面
            handleEdit: function (index, row) {
                console.log(row)
                this.addFormVisible = true;
                this.passwordShow = false;
                row.qx = row.qx+ '';
                //row.gsdm = row.gsdm+ '';
                this.addForm = Object.assign({}, row); //合并对象防止改动页面内容
                this.addForm.mm = '';
            },
            //显示新增界面
            handleAdd: function () {
                this.passwordShow = this.addFormVisible = true;
                this.addForm = {};
            },
            //提交
            addSubmit: function () {
                console.log(this.addForm)
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.addForm.rzdm = this.$rzdm;
                        let url = this.$url + '/user/add';
                        if(this.addForm.id){
                            url = this.$url + '/user/update';
                            this.addForm.mm = '';
                        }
                        this.$axios.post(url,this.$qs.stringify(this.addForm)).then((res) => {
                            let data = res.data;
                            if(res.data.resCode == 200){
                                this.$message.success('保存成功!');
                                this.getData();
                                this.addFormVisible = false;
                                this.$refs.addForm.resetFields()
                            }else{
                                this.$message.error(res.data.resMsg);
                            }
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            resetPwd(index, row){
                this.addForm = Object.assign({}, row); //合并对象防止改动页面内容
                this.addForm.mm = '';
                this.resetPassword = true;
            },
            resetPwdSubmit(){
                let param = {
                    id: this.addForm.id,
                    newword: this.newPassword,
                    rzdm: this.$rzdm
                };
                let url = this.$url + '/user/changepassword';
                this.$axios.post(url,this.$qs.stringify(param)).then(res=>{
                    if(res.data.resCode == 200){
                        this.getData()
                        this.resetPassword = false;
                        this.$message({
                            message: '密码修改成功,请牢记!',
                            type: 'success'
                        });
                    }else{
                        this.$message.error(res.data.resMsg);
                    }
                })
            }
        },
    }
</script>

<style >
    /*.el-input__inner, .el-textarea__inner {*/
        /*color: #707276;*/
        /*font-weight: normal;*/
    /*}*/
    /*.el-textarea__inner {*/
        /*font-size: 16px;*/
    /*}*/
    /*.el-input.is-disabled .el-input__inner, .el-textarea.is-disabled .el-textarea__inner {*/
        /*background-color: #FAFAFA;*/
        /*border-color: #DCDFE6;*/
        /*color: #707276;*/
    /*}*/


    .file-dialog .content{
        width: 100%;
        height: 800px;
        background: #cccccc;
    }
    .el-dialog--small {
        width: 80%;
    }
</style>


