import babelpolyfill from 'babel-polyfill'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';    // 默认主题
import Vue from 'vue'
import App from './App'

//import 'element-ui/lib/theme-default/index.css'
import './assets/theme/element-#0B911B/index.css'
import VueRouter from 'vue-router'
import store from './vuex/store'
import Vuex from 'vuex'
//import axios from './common/js/axios'
import axios from 'axios'

import qs from 'qs'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import routes from './routes'
import 'font-awesome/css/font-awesome.min.css'
import util from './common/js/util'

Vue.prototype.$util = util;
Vue.prototype.$qs = qs;
Vue.prototype.$axios = axios;
Vue.prototype.$url = process.env.NODE_ENV == 'development' ? 'http://182.151.22.247:8083' : location.origin+ '/TaxiAccount';
// Vue.prototype.$url = 'http://36.111.191.34:8088';
Vue.use(ElementUI);
Vue.use(VueRouter);
Vue.use(Vuex);

// Vue.directive('focus', function (el) {
//     el.focus()
//     console.log(el);
//
// })

//NProgress.configure({ showSpinner: false });

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  //NProgress.start();

  if (to.path == '/login') {
    sessionStorage.removeItem('userInfo');
  }
  let user = JSON.parse(sessionStorage.getItem('userInfo'));
  if (!user && to.path != '/login') {
      next({ path: '/login' })
  }else {
      next();
      if(user){
          Vue.prototype.$rzdm = user.code; //登录成功后添加全局认证码
      }
  }
});



//router.afterEach(transition => {
//NProgress.done();
//});

new Vue({
  //el: '#app',
  //template: '<App/>',
  router,
  store,
  //components: { App }
  render: h => h(App)
}).$mount('#app')




